import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
export default {
	data() {
		return {
			  id:'',//路由传过来的用户userid看是自己点的自己还是点的别人的
			  list: [],
			  page: 1, // 关注当前页
			  hasMore: false, // 提示暂无更多,
			  allLoaded: false, // 分页加载
			  isMe: false,
			  page_id: '',
			  loading:false,
			  allowload:false,
			  isPraise: false,
			  detailId:'',
			  showNum:60,
			  n:1000000,
			  indexCurrent:null,
			  showTitle:false
		}
	},
	// onShareAppMessage(res) {
	// 	this.$api.reports_transpond({
	// 		token: uni.getStorageSync("token"),
	// 		user_id: uni.getStorageSync("userId"),
	// 		id:res.target.dataset.id
	// 	}).then(res=>{
	// 		// console.log(res)
	// 	})
	//     if (res.from === 'button') {// 来自页面内分享按钮
	//     }
	//     return {
	//       title: res.target.dataset.title,
	// 	  imageUrl:res.target.dataset.img,
	// 	  path: '/pagesMy/InfoDetail/InfoDetail?id='+res.target.dataset.id
	//     }
	// },
	onReachBottom(){
		if(this.allowload){
			// console.log(1)
			this.allowload = false				
			if(this.list.length>0){
				this.page_id = this.list[this.list.length-1].id
			}
			this.loading = true
			this.getData()
			// setTimeout(() => {
				
			// 	uni.showToast({
			// 		title: '加载成功',
			// 		duration: 1000,
			// 		icon:'none'
			// 	});
			// }, 1500)	
		}
	},
	onPullDownRefresh(){
		this.page_id = ''
		this.getData()
		setTimeout(function () {
		        uni.stopPullDownRefresh();
				uni.showToast({
				    title: '刷新成功',
				    duration: 1000,
					icon:'none'
				});
		  }, 1000);
	},
	onShow() {
		// if(this.indexCurrent){
		// 	var videoContextPrev = uni.createVideoContext(this.indexCurrent)
		// 	videoContextPrev.pause()
		// }
		this.indexCurrent = null
		// 控制举报的
		if(uni.getStorageSync('reportid')){
			this.list.forEach((item,index)=>{
				if(uni.getStorageSync('reportid') == item.id){
					item.is_report = 1
				}
			})
			setTimeout(function() {
				uni.removeStorageSync('reportid')
			}, 1500);
		}
		// 详情删除,需要到界面过滤出去
		if(uni.getStorageSync('delid')){
			this.list = this.list.filter((item,index) => item.id != uni.getStorageSync('delid'))
			setTimeout(function() {
				uni.removeStorageSync('delid')
			}, 1500);
		}
		
		// 详情点赞之后回到界面需要显示已经点赞
		if(uni.getStorageSync('praiseid')){
			console.log(uni.getStorageSync('praiseid'))
			this.list.forEach((item,index)=>{
				if(uni.getStorageSync('praiseid') == item.id){
					item.is_upvotes = true
					item.upvotes++
				}
			})
			
			setTimeout(function() {
				uni.removeStorageSync('praiseid')
			}, 1500);
		}
		
		if(uni.getStorageSync('unpraiseid')){
			this.list.forEach((item,index)=>{
				if(uni.getStorageSync('unpraiseid') == item.id){
					item.is_upvotes = false
					if(item.upvotes>0){
						item.upvotes--
					}
					
				}
			})
			setTimeout(function() {
				uni.removeStorageSync('unpraiseid')
			}, 1500);
		}
	},
	onHide() {
		this.n = 1000000
		if(this.indexCurrent){
			var videoContextPrev = uni.createVideoContext(this.indexCurrent)
			videoContextPrev.pause()
		}
		
	},
	onLoad(e) {
		
		uni.showLoading({
		    title: '加载中'
		});
		
		// 这个是点击他的资讯圈或者是我的资讯圈带过来的 那个人的id
		this.id = e.id
		
		if (e.id != uni.getStorageSync('userId')) {
		      this.isMe = false
		    } else {
		      this.isMe = true
		 }
		 this.getData()

	},
	methods: {
		// 发布
		Issue(){
			uni.navigateTo({
				url:'../../pagesMy/informationSend/informationSend'
			})
		},
		// 视频播放
		videoPlay(e){
			this.indexCurrent=e
		},
		// 视频播放
		video_play(e){
			// console.log(e)
			var curIdx = e.currentTarget.id;
			      // 没有播放时播放视频
			      // console.log(curIdx)
			      if (!this.indexCurrent) {				        
			        this.indexCurrent=curIdx
			        var videoContext = uni.createVideoContext(curIdx) //这里对应的视频id
			        videoContext.play()
			      } else { // 有播放时先将prev暂停，再播放当前点击的current
			        var videoContextPrev = uni.createVideoContext(this.indexCurrent)//this是在自定义组件下，当前组件实例的this，以操作组件内 video 组件（在自定义组件中药加上this，如果是普通页面即不需要加）
			        if (this.indexCurrent != curIdx) {
			          // console.log(123)
			          videoContextPrev.pause()
			          this.indexCurrent=curIdx				          
			          var videoContextCurrent = uni.createVideoContext(curIdx)
			          videoContextCurrent.play()
			        }
			}
		},
		// 删除自己的资讯圈
		del_zixun(it,index){
			Dialog.confirm({
			  title: '提示',
			  message:'您确定删除吗？'
			}).then(() => {
				this.$api.del_user_dynamic({
					token: uni.getStorageSync("token"),
					user_id: uni.getStorageSync("userId"),
					dynamic_id:it.id
				}).then(res=>{
					uni.showToast({
					    title: res.data.msg,
					    duration: 2000,
						icon:'none'
					});
					this.list = this.list.filter((item,index) => item.id != it.id)
				})
			  
			}).catch(() => {
			  
			});
				
		},
		// 举报资讯圈
		jubao_zixun(item,index){
			uni.navigateTo({
				url:'../../pagesMy/Report/Report?type=1&id='+item.id +"&person=222"
			})
		},
		// 显示举报或者删除、
		Msg(item,ind){
			if(this.n === ind){
				this.n = 1000000
			}else{
				this.n = ind
			}
		},
		// 获取头像列表,看点赞人用的
		getAvatar () {
		      this.$api.NewsUpvotesAvatar({
		        token: uni.getStorageSync("token"),
		        user_id: uni.getStorageSync("userId"),
		        id: this.id
		      }).then(res => {
		        if (res.data.code === '200') {
		          // this.upvotes = res.data.data.upvotes // 点赞数量
		          this.isPraise = res.data.data.is_upvotes // 是否点赞
		        }
		      })
		},
		// 详情
		detail(id){
			this.detailId = id
			uni.navigateTo({
				url:'../../pagesMy/InfoDetail/InfoDetail?id='+id
			})
		},
		// 跳转个人主页
		MyInfo(id){
			uni.navigateTo({
				url:'../../pagesMy/MyInfo/MyInfo?id='+id
			})
		},
		 // 图片预览
		preview(ind,img){
			  uni.previewImage({
				 urls: img,
				 current:ind
			 });
		},
		  // 点赞
		praise(item){
			   if (item.is_upvotes) {
			          this.$api.NewsUpvotes({
			            token: uni.getStorageSync("token"),
			            user_id: uni.getStorageSync("userId"),
			            id: item.id
			          }).then(res => {
			            // this.$toast(res.msg)
						uni.showToast({
						    title: res.data.msg,
						    duration: 2000,
							icon:'none'
						});
			            item.is_upvotes = false
			            item.upvotes = item.upvotes - 1
			          })
			        } else {
			          this.$api.NewsUpvotes({
			            token: uni.getStorageSync("token"),
			            user_id: uni.getStorageSync("userId"),
			            id: item.id
			          }).then(res => {
			            uni.showToast({
			                title: res.data.msg,
			                duration: 2000,
			            	icon:'none'
			            });
			            item.is_upvotes = true
			            item.upvotes = item.upvotes + 1
			          })
			 }     
		},
		getData () {
		      this.$api.MyselfUserDynamics({
		        token: uni.getStorageSync("token"),
		        user_id: uni.getStorageSync("userId"),
		        other_id: this.id,
		        page_id: this.page_id
		      }).then(res => {
				  console.log(res)
				  uni.hideLoading();
		        if (res.data.code === '200') {					
		            if(!this.page_id){
						this.list = res.data.data
		            	if(res.data.data.length>=10){
		            		this.allowload = true
		            	}else{
		            		this.showTitle = true
		            		this.allowload = false
		            	}														
		            }else{
						this.list = this.list.concat(res.data.data)
		            	if(res.data.data.length>=10){
		            		this.allowload = true
		            	}else{
		            		this.allowload = false
							this.showTitle = true
		            	}	
		            }
					this.loading = false
		        } else if (res.data.code === '1000') {
		          uni.clearStorageSync()
		          uni.showToast({
		              title: '登录过期，请重新登录',
		              duration: 2000,
		          	icon:'none'
		          });
		          
		          uni.reLaunch({
		          	url: '../../pages/login/login'
		          })
		        
				}
				 
		    })
		}
	}
}
